<template>
  <div class="row">
    <div class="col-md-8 col-sm-12">
      <!-- new pizza -->
      <app-new-pizza></app-new-pizza>
    </div>
    <div class="col-md-4 col-sm-12">
      <!-- 品种展示 -->
      <h3 class="text-muted my-5">菜单</h3>
      <table class="table">
        <thead class="table table-defaul">
          <tr>
            <th>品种</th>
            <th>删除</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.key">
          <tr>
            <td>{{item.name}}</td>
            <td>
              <button @click="deleteData(item)" class="btn btn-outline-danger btn-sm">&times;</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import NewPizza from "./NewPizza";
export default {
  data() {
    return {
      //   getMenuItems: []
    };
  },
  computed: {
    getMenuItems: {
      get() {
        return this.$store.getters.getMenuItems;
      },
      set() {}
    }
  },
  methods: {
    deleteData(item) {
      fetch(
        "https://wd5712155014lycgvl.wilddogio.com/menu/" + item.id + ".json",
        {
          method: "DELETE",
          headers: {
            "Content-type": "application/json"
          }
        }
      )
        .then(res => res.json())
        .then(data => {
          this.$store.commit("removeItem", item);
        })
        .catch(err => console.log(err));
    }
  },
  components: {
    "app-new-pizza": NewPizza
  },
  created() {
    fetch("https://wd5712155014lycgvl.wilddogio.com/menu.json")
      .then(res => res.json())
      .then(data => {
        let menuArray = [];
        for (const key in data) {
          data[key].id = key;
          menuArray.push(data[key]);
        }
        // 把数据放到vuex中
        // this.getMenuItems = menuArray;
        this.$store.commit("setMenuItems", menuArray);
      });
  }
};
</script>
