<template>
  <div class="row">
    <!-- 菜单 -->
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>尺寸</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.key">
          <tr>
            <td>
              <strong>{{item.name}}</strong>
            </td>
          </tr>
          <tr v-for="option in item.options" :key="option.size">
            <td>{{option.size}}</td>
            <td>{{option.price}}</td>
            <td>
              <button @click="addToBasket(item,option)" class="btn btn-sm btn-outline-success">+</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- 购物车 -->
    <div class="col-md-4">
      <div v-if="baskets.length>0">
        <table class="table">
          <thead class="thead-default">
            <tr>
              <th>数量</th>
              <th>品种</th>
              <th>价格</th>
            </tr>
          </thead>
          <tbody v-for="item in baskets" :key="item.id">
            <tr>
              <td>
                <button @click="decreaceQuantity(item)" class="btn btn-sm">-</button>
                <span>{{item.quantity}}</span>
                <button @click="increaceQuantity(item)" class="btn btn-sm">+</button>
              </td>
              <td>{{item.name}}{{item.size}}</td>
              <td>{{item.quantity*item.price}}</td>
            </tr>
          </tbody>
        </table>
        <p>总价：{{total+ 'RMB'}}</p>
        <button class="btn btn-success btn-block">提交</button>
      </div>
      <div v-else>{{basketText}}</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      baskets: [],
      basketText: "购物车没有任何商品"
      // getMenuItems: {}
    };
  },
  created() {
    this.fetchData();
  },
  computed: {
    getMenuItems() {
      return this.$store.getters.getMenuItems;
    },
    total() {
      let totalCost = 0;
      for (const index in this.baskets) {
        let totalItem = this.baskets[index];
        totalCost += totalItem.price * totalItem.quantity;
      }
      return totalCost;
    }
  },
  methods: {
    fetchData() {
      fetch("https://wd5712155014lycgvl.wilddogio.com/menu.json")
        .then(res => res.json())
        .then(data =>
          // 把数据保存到vuex中
          this.$store.commit("setMenuItems", data)
        );
    },
    addToBasket(item, option) {
      let basket = {
        id: Date(),
        name: item.name,
        price: option.price,
        size: option.size,
        quantity: 1
      };
      if (this.baskets.length > 0) {
        //过滤
        let result = this.baskets.filter(
          basket => basket.name === item.name && basket.price === option.price
        );
        if (result != null && result.length > 0) {
          result[0].quantity++;
        } else {
          this.baskets.push(basket);
        }
      } else {
        this.baskets.push(basket);
      }
    },
    decreaceQuantity(item) {
      if (item.quantity <= 0) {
        this.reMoveFormItem(item);
      } else {
        item.quantity--;
      }
    },
    increaceQuantity(item) {
      item.quantity++;
    },
    reMoveFormItem(item) {
      this.baskets.splice(this.baskets.indexOf(item), 1);
    }
  }
};
</script>
